var app = getApp();
const common = require("../../../publicjs/common.js");
import uniSwiperDot from '@/components/uni-swiper-dot/uni-swiper-dot.vue'; //轮播

export default {
	components: {
		uniSwiperDot
	},
	data() {
		return {
			//轮播图
			bannerUrl: [
					{
						colorClass: 'uni-bg-red',
						url: app.globalData.oosHost + 'sys/index_banner.jpg',
						content: '内容 A'
					},
					{
						colorClass: 'uni-bg-green',
						url: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/muwu.jpg',
						content: '内容 B'
					},
					{
						colorClass: 'uni-bg-blue',
						url: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/cbd.jpg',
						content: '内容 C'
					}
			],
			current: 0,
			dotsStyles: {},
			tabs: [{
				'title': "活动详情"
			  },
			  {
				'title': "组队"
			  }
			],
			activeIndex: 0,
			sliderOffset: 0,
			sliderLeft: 0,
			vo: {
				document: ""
			}
		}
	},
	onLoad:function(options){
		 commonn.requestURL({
		      url: "/activity/detail",
		      method: "GET",
		      params: {
		        id: options.id
		      }
		    }).then(res => {
				this.vo = res.data.vo;
		    });
			//获取字体
		   common.fonts();
	},
	methods: {
		//预览文档
		viewDoc: function() {
			 uni.downloadFile({
			      url: this.oosHost + this.vo.document,
			      success(res) {
					if (res.statusCode === 200) {
						const filePath = res.tempFilePath;
						wx.openDocument({
						  filePath,
						  success(res) {
							console.log('打开文档成功')
						  }
						})
					}
			      }
			    })
		},
		//下载文档
		downLoadDoc:function() {
			 uni.downloadFile({
			      // 示例 url，并非真实存在
			      url: this.oosHost + this.vo.document,
			      success(res) {
			        const filePath = res.tempFilePath
			        wx.saveFile({
			          tempFilePath: filePath,
			          success(res) {
			            uni.showModal({
			              title: '提示',
			              content: '下载成功',
			            })
			          },
			          fail: function(res) {
			            console.log(res);
			          }
			        })
			      }
			    })
		}
	},
	
}
